import { Component } from "react";
import axios from 'axios';
const baseurl = process.env.REACT_APP_API_BASE_URL;
class Register extends Component{
    constructor(props){
        super(props);
        this.state={
            loading:false,
            showpassword:false,
            username:"",
            email:"",
            password:"",
            academy:0,
            erroremail:"",
            errorpassword:"",
            memberID:"",
            agree:false,
            language:JSON.parse(localStorage.language).language
        }
    }

    handleChange = filedName => e=>{
        this.setState({
            [filedName]:e.target.value
        })
    }

    handleSubmit = e =>{
        e.preventDefault();
        const {username, email, password, academy, memberID:memberID} = this.state
        var data = JSON.stringify({"email":email,"password":password, "name":username, "academy":academy, "memberID":memberID});
        var config = {
          method: 'post',
          url: `${baseurl}/api/signup`,
          headers: { 
            'Content-Type': 'application/json'
          },
          data : data
        };
        axios(config)
        .then((response)=>{
            this.setState({loading:false});
            this.props.history.push('/registersucess')
        })
    }

    render()
    {
        const {username, email, password, erroremail, errorpassword, academy, agree,memberID} = this.state
        return(
            <>

                <div className="logo sp">
                    <img src="./assets/img/bg_logo_black.png" alt="" />
                </div>
                <main id="register">
                    <header>
                        <h2>新規登録</h2>
                    </header>
                    <section className="container">
                        <img src="./assets/img/bg_logo.png" alt="" className="pc" />
                        <div className="input-box">
                            <label htmlFor="">ユーザー名</label>
                            <input type="text" value={username} onChange={this.handleChange("username")}/>
                        </div>
                        <div className="input-box">
                            <label htmlFor="">メールアドレス</label>
                            <input type="email"  value={email} onChange={this.handleChange("email")}/>
                        </div>
                        <div className="input-box">
                            <label htmlFor="">パスワード</label>
                            <input type="password" value={password} onChange={this.handleChange("password")}/>
                        </div>
                        <div className="input-box">
                            <label htmlFor="">所属アカデミー</label>
                            <select value={academy} onChange={this.handleChange("academy")}>
                                <option value={0} disabled hidden>〇〇〇〇アカデミー</option>
                            </select>
                        </div>
                        <div className="input-box">
                            <label htmlFor="">会員ID</label>
                            <input type="text" value={memberID} onChange={this.handleChange("memberID")}/>
                        </div>
                        <div className="input-box input-checkbox">
                            <label htmlFor="privacy">
                                <input type="checkbox" id="privacy" value={agree} onChange={this.handleChange("agree")}  id="privacy"/>
                                <span></span>
                                <a href="/privacy">利用規約</a>に同意します<div className="hissu">必須</div>
                            </label>
                        </div>
                        <a className="yellow-btn" onClick={this.handleSubmit}>
                            <img src="./assets/img/icon_signup1.png" alt="" />
                            <span>登録</span>
                        </a>
                        <p>アカウントをお持ちですか？<a href="/login">ログイン</a></p>
                    </section>
                </main>
            </>
        )
    }
}

export default Register;