import { Component } from "react";
import axios from 'axios';
const baseurl = process.env.REACT_APP_API_BASE_URL;
const academyList = [
    "アカデミー1",
    "アカデミー2",
    "アカデミー3"
]
class Header extends Component{
    constructor(props)
    {
        super(props)
        this.state={
            userName:"",
            academy:"",
            daypoint:0,
            point:0,
            weekpoint:0,
            monthpoint:0,
        }
    }
    componentDidMount(){
        this.getProfile();
    }

    getProfile(){
        var userData =  JSON.parse(localStorage.getItem("userData")) || null;
        var token = userData.token
        var config = {
            method: 'get',
            url: `${baseurl}/api/getProfile`,
            headers: { 
              'Content-Type': 'application/json',
              'Authorization': 'Bearer ' + token,
            },
            data : {}
          };
          axios(config)
          .then(async (response)=>{
                var userData= response.data.user;
                console.log("asdf")
                this.setState({
                    userName:userData.name,
                    academy:userData.academy,
                    daypoint:parseInt(userData.daypoint).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ","),
                    point:parseInt(userData.point).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ","),
                    weekpoint:parseInt(userData.weekpoint).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ","),
                    monthpoint:parseInt(userData.monthpoint).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ","),
                })
                
           })
          .catch((error)=>{
           });
    }

    render()
    {
        const {userName, academy, daypoint, weekpoint, monthpoint, point} = this.state
        return(
            <>              
                <div className="header">
                    <div className="header-img">
                        <img src="/assets/img/avatar.jpg" alt="" />
                    </div>
                    <table className="header-table">
                        <tbody>
                            <tr>
                                <td>ユーザー名</td>
                                <td>{userName}</td>
                            </tr>
                            <tr>
                                <td>所属アカデミー</td>
                                <td>{academyList[academy]}</td>
                            </tr>
                            <tr>
                                <td>保有ポイント</td>
                                <td>{point}</td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </>
        )
    }
}

export default Header;