import { Component } from "react";
import Header from './Header';
class Profile extends Component{

    render()
    {
        return(
            <>
               <main id="information">
                    <header>
                        <h2>登録情報</h2>
                    </header>
                    <section className="container-main">
                        <Header/>
                        <div className="container1">
                            <div className="crumb">
                                <p><a href="">トップ</a> 登録情報</p>
                            </div>
                            <div className="information-container">
                                <div className="information-box">
                                    <h4>ユーザー名</h4>
                                    <p>XXXX XXXX</p>
                                </div>
                                <div className="information-box">
                                    <h4>所属アカデミー</h4>
                                    <p>XXXX XXXX</p>
                                </div>
                                <div className="information-box">
                                    <h4>生徒ID</h4>
                                    <p>XXXX XXXX</p>
                                </div>
                                <div className="information-box">
                                    <h4> 国</h4>
                                    <p>XXXX XXXX</p>
                                </div>
                                <div className="information-box">
                                    <h4>登録Eメールアドレス</h4>
                                    <p>XXXX XXXX</p>
                                </div>
                                <div className="information-box">
                                    <h4>登録ウォレットアドレス<br />（受け取り用）</h4>
                                    <p>XXXX XXXX</p>
                                </div>
                            </div>
                            <div className="information-container">
                                <a href="" className="yellow-btn information-btn">
                                    <img src="/assets/img/icon_information.png" alt="" />
                                    <span>登録情報の変更・ウォレット設定</span>
                                </a>
                            </div>
                        </div>
                    </section>
                </main>
            </>
        )
    }
}

export default Profile;