import { Component } from "react";
import axios from 'axios';
const baseurl = process.env.REACT_APP_API_BASE_URL;
const academyList = [
    "アカデミー1",
    "アカデミー2",
    "アカデミー3"
]
class Mypage extends Component{

    constructor(props)
    {
        super(props)
        this.state={
            currency:"0.00",
            userName:"",
            academy:"",
            daypoint:0,
            point:0,
            weekpoint:0,
            monthpoint:0,
            lastdate:"00",
            lasttime:"00",
            lastminute:"00",
            lastsecond:"00"
        }
    }

    componentDidMount(){
        this.getProfile();
    }
    
    componentWillUnmount() {
        clearInterval(this.interval)
    }

    getProfile(){
        var userData =  JSON.parse(localStorage.getItem("userData")) || null;
        var token = userData.token
        var config = {
            method: 'get',
            url: `${baseurl}/api/getProfile`,
            headers: { 
              'Content-Type': 'application/json',
              'Authorization': 'Bearer ' + token,
            },
            data : {}
          };
          axios(config)
          .then(async (response)=>{
                var userData= response.data.user;
                this.setState({
                    userName:userData.name,
                    academy:userData.academy,
                    daypoint:parseInt(userData.daypoint).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ","),
                    point:parseInt(userData.point).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ","),
                    weekpoint:parseInt(userData.weekpoint).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ","),
                    monthpoint:parseInt(userData.monthpoint).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ","),
                })
                this.getLastDate((userData.lastdate).replace("(","").replace(")","").replace(",",""))
                this.interval = setInterval(() => this.getLastDate((userData.lastdate).replace("(","").replace(")","").replace(",","")), 1000);
                
           })
          .catch((error)=>{
                if (error.response) {
                    if(error.response.status==401){
                        localStorage.removeItem("userData");
                        window.location.assign('/');
                    }
                } 
           });
    }

    componentWillUnmount() {
        clearInterval(this.interval)
    }

    getLastDate(date){
        var lastdate = new Date(date)
        var today = new Date();
        console.log(lastdate)
        console.log(today)
        const diff = lastdate - today;
        if(diff<0)
        {
            return;
        }
        else{
            var seconds = Math.floor(diff / 1000);
            var date = parseInt(seconds / (3600 * 24))
            seconds = seconds % (3600 * 24)
            var time = parseInt(seconds / 3600);
            seconds = seconds % 3600
            var minutes = parseInt(seconds / 60)
            seconds = seconds % 60
            this.setState({
                lastdate:date,
                lasttime:time,
                lastminute:minutes,
                lastsecond:seconds
            })
        }
    }

    render()
    {
        const {currency,userName, academy, daypoint, weekpoint, monthpoint, point, lastdate, lasttime, lastminute, lastsecond} = this.state
        return(
            <>
              <main>
                <header>
                    <h2>マイページ</h2>
                </header>
                <section className="container-main">
                    <div className="header">
                        <div className="header-img">
                            <img src="/assets/img/avatar.jpg" alt="" />
                        </div>
                        <table className="header-table">
                            <tbody>
                            <tr>
                                <td>ユーザー名</td>
                                <td>{userName}</td>
                            </tr>
                            <tr>
                                <td>所属アカデミー</td>
                                <td>{academyList[academy]}</td>
                            </tr>
                            <tr>
                                <td>保有ポイント</td>
                                <td>{point}</td>
                            </tr>
                            </tbody>
                        </table>
                    </div>
                    <div className="container1">
                        <div className="crumb">
                            <p><a href="">トップ</a> マイページ</p>
                        </div>
                        <div className="top-container">
                            <div className="point-info">
                                <svg xmlns="http://www.w3.org/2000/svg" version="1.1" fill="transparent" style={{overflow:"visible"}}>
                                    <rect rx="15" ry="15" height="100%" width="100%" stroke="white" />
                                </svg>
                                <div className="point-info-title">
                                    <h3 className="point-info-title-spec">総保有ポイント</h3>
                                </div>
                                <div className="point-info-main">
                                    <h4>{point}<span>ポイント</span></h4>
                                </div>
                            </div>
                            <div className="point-info-detail">
                                <div>
                                    <h3>
                                        <span>月間</span><span>獲得ポイント</span>
                                    </h3>
                                    <h4>{monthpoint}</h4>
                                    <p>ポイント</p>
                                </div>
                                <div>
                                    <h3>
                                        <span>週間</span><span>獲得ポイント</span>
                                    </h3>
                                    <h4>{weekpoint}</h4>
                                    <p>ポイント</p>
                                </div>
                                <div>
                                    <h3>
                                        <span>日間</span><span>獲得ポイント</span>
                                    </h3>
                                    <h4>{daypoint}</h4>
                                    <p>ポイント</p>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="mypage-container">
                        <div className="container1">
                            <div className="remaining">
                                <h3>コース終了まで残り</h3>
                                <p><span>{lastdate}</span>日と<span>{lasttime}</span>時間<span>{lastminute}</span>分<span>{lastsecond}</span>秒</p>
                            </div>
                        </div>
                        <div className="point-history">
                            <h3>ポイント履歴</h3>
                            <div className="point-history-tab-container">
                                <div className="point-history-tab point-history-tab-active" >交換履歴</div>
                                <div className="point-history-tab"  >送受信履歴</div>
                            </div>
                            <div className="point-history-content-container point-history-content-container-show" id="point-container1">
                                <div className="point-history-content">
                                    <div className="point-history-content-title">
                                        <div>日付</div>
                                        <div>取引内容</div>
                                        <div>ポイント</div>
                                    </div>
                                    <div className="point-history-content-desc">
                                        <div>2021年7月30日</div>
                                        <div>XXXXXXXXXXXXXXXXXXXX</div>
                                        <div><span>1,000</span>ポイント</div>
                                    </div>
                                </div>
                            </div>
                            <div className="point-history-content-container" id="point-container2">
                            </div>
                        </div>
                    </div>
                </section>
            </main>
            </>
        )
    }
}

export default Mypage;