import { Component } from "react";

const en = {
    firstview:"Welcome",
    signup:"Sign Up",
    login:"Log in",
    japanese:"Japanese",
    english:"English"
}

const jp = {
    firstview:"初期画面",
    signup:"新規登録",
    login:"ログイン",
    japanese:"日本語",
    english:"英語"
}

class Top extends Component{

    constructor(props)
    {
        super(props);
        this.state = {
            language:"jp"
        }
    }

    handleChangeLanguage = (event)=>{
        localStorage.setItem("language", JSON.stringify({language:event.target.value}))
        this.setState({language:event.target.value})
    }

    componentDidMount(){
        localStorage.setItem("language", JSON.stringify({language:"jp"}))
    }

    render()
    {
       const {language} = this.state
        return(
            <main id="index">
                <header>
                    <h2>{eval(language).firstview}</h2>
                </header>
                <section className="container">
                    <img src="./assets/img/bg_logo.png" alt="" />
                    <a className="white-btn register-btn" href="/register">
                        <img src="./assets/img/icon_signup.png" alt="" />
                        <span>{eval(language).signup}</span>
                    </a>
                    <a className="yellow-btn register-btn" href="/login">
                        <img src="./assets/img/icon_signin.png" alt="" />
                        <span>{eval(language).login}</span>
                    </a>
                    <select onChange={this.handleChangeLanguage} className="first-select" name="language" id="language">
                        <option value="" selected disabled hidden>言語を選択</option>
                        <option value="en">{eval(language).english}</option>
                        <option value="jp">{eval(language).japanese}</option>
                    </select>
                </section>
            </main>


        )
    }
}

export default Top;