import { Component } from "react";

class ResigerComplete extends Component{

    render()
    {
        return(
            <>
                <div className="logo sp">
                    <img src="./assets/img/bg_logo_black.png" alt="" />
                </div>
                <main>
                    <header>
                        <h2>新規登録</h2>
                    </header>
                    <section className="container">
                        <img src="./assets/img/bg_logo.png" alt="" className="pc" />
                        <h4>
                            仮登録が完了しました。<br />
                            <br />
                            メールから本登録を<br />
                            完了して下さい。
                        </h4>
                        <a href="/login" className="white-btn">ログイン画面に戻る</a>
                    </section>
                </main>
            </>
        )
    }
}

export default ResigerComplete;